import React, {Component} from 'react';
import Pubsub from "pubsub-js";

class Navhead extends Component {
    state={
        show:false
    }
    handleclick=()=>{
        this.setState({show:!this.state.show})
        Pubsub.publish("show",!this.state.show)
    }
    componentDidMount() {
        Pubsub.subscribe("show",(_,data)=>{
            console.log("navhead")
            this.setState({show:data})
        })
    }

    render() {
        return (
            <div className="bg-white flex h-12 w-full box-border px-6 fixed top-0 right-0 border z-50">
                <div className="flex-none  font-black subpixel-antialiased flex ">
                    <span className="text-3xl content-center">zhangxurui</span>
                </div>
                <div className="flex-1"></div>
                <div className="flex flex-none justify-center items-center md:hidden" onClick={this.handleclick}>
                    {/*svgpathsvg
                     M x yxyh llxy
                     https://cloud.baidu.com/article/3294183
                     svg x y width height
                     width heightsvgsvgwidth heightsvgsvgsvg
                     svgsvgsvgviewboxxy
                     */}
                    {
                        this.state.show ?
                            <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1}
                                 stroke="currentColor" className="w-9 h-9">
                                <path strokeLinecap="round" strokeLinejoin="round" d="M6 18 18 6M6 6l12 12"/>
                            </svg> :
                            <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1}
                                 stroke="currentColor" className="w-9 h-9">
                                <path strokeLinecap="round" strokeLinejoin="round" d="M3.75 6.75h16.5M3.75 12h16.5m-16.5 5.25h16.5"/>
                            </svg>

                        // : <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 48 48" strokeWidth={1.5}
                        //        stroke="currentColor" className="w-12 h-12 box-border">
                        //     <path strokeLinecap="round" strokeLinejoin="round" d="M10 16h24 M10 24h24 M10 32 h24"/>
                        // </svg>

                    }
                </div>
            </div>
        );
    }
}

export default Navhead;